import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';

import { Survey } from 'src/app/services/surveys/survey';
import { IListItemsPage } from 'src/app/services/list-items-service';
import { SurveysService } from 'src/app/services/surveys/surveys.service';
import { SharepointService } from 'src/app/services/sharepoint/sharepoint.service';

@Component({
  selector: 'app-polls-archive-page',
  templateUrl: './polls-archive-page.component.html',
  styleUrls: ['./polls-archive-page.component.scss']
})
export class PollsArchivePageComponent implements OnInit {
  public items: Survey[];
  private pages: IListItemsPage<Survey>[] = [];
  private index = 0;
  private userId = 0;
  public hasPrev = false;
  public hasNext = false;
  isLiked = false;

  constructor(private surveysService: SurveysService,
    private route: ActivatedRoute,
    private sharepointService: SharepointService) {}

  ngOnInit() {
    this.userId = this.route.snapshot.data.userId;
    this.getNotLiked();
  }

  public getNext(): void {
    if (this.hasNext) {
      if (this.index === (this.pages.length - 1)) {
        this.pages[this.index]
          .getNext()
          .subscribe((page: IListItemsPage<Survey>) => {
            this.index++;
            this.items = page.items;
            this.hasNext = page.hasNext;
            this.hasPrev = true;
            this.pages.push(page);
          });
      } else {
        this.index++;
        this.items = this.pages[this.index].items;
        this.hasNext = this.pages[this.index].hasNext;
        this.hasPrev = true;
      }
    }
  }

  public getPrev(): void {
    if (this.hasPrev) {
      this.index--;
      this.items = this.pages[this.index].items;
      this.hasNext = true;
      this.hasPrev = this.index > 0;
    }
  }

  public reset(): void {
    if (this.hasPrev) {
      this.index = 0;
      this.items = this.pages[this.index].items;
      this.hasNext = true;
      this.hasPrev = false;
    }
  }

  public goTo(survey: Survey) {
    window.open(survey.link, '_blank');
    this.addLike(survey);
  }

  public getLiked() {
    this.isLiked = true;
    this.surveysService.getItemsPaged({
      filter: `LikedBy/Id eq ${this.userId} or slEndDate le datetime'${new Date().toISOString()}'`,
      orderBy: [['Created', false]],
      top: 15
    }).subscribe(page => {
      this.pages = [];
      this.index = 0;
      this.items = page.items;
      this.hasNext = page.hasNext;
      this.hasPrev = false;
      this.pages.push(page);
    });
  }

  public getNotLiked() {
    this.isLiked = false;
    this.surveysService.getItemsPaged({
      filter: `(LikedBy/Id ne ${this.userId}) and (slEndDate gt datetime'${new Date().toISOString()}' or slEndDate eq null)`,
      orderBy: [['Created', false]],
      top: 15
    }).subscribe(page => {
      this.pages = [];
      this.index = 0;
      this.items = page.items;
      this.hasNext = page.hasNext;
      this.hasPrev = false;
      this.pages.push(page);
    });
  }

  public addLike(item: Survey): void {
    if (item.likedByIds.indexOf(this.userId) < 0) {
      this.surveysService.addLike({id: item.id}).subscribe();
      item.likedByIds.push(this.userId);
      item.likesCount++;
    }
  }

  create() {
    window.location.href = this.sharepointService.getCurrentUserContext().webServerRelativeUrl
      + '/Lists/slSurvey/NewForm.aspx?Source='
      + encodeURIComponent(window.location.href);
  }

  edit(item: any) {
    window.location.href = this.sharepointService.getCurrentUserContext().webServerRelativeUrl
      + `/Lists/slSurvey/EditForm.aspx?ID=${item.id}&Source=`
      + encodeURIComponent(window.location.href);
  }

  remove(item: any) {
    this.surveysService.recycleItem(item)
      .subscribe(() => this.isLiked ? this.getLiked() : this.getNotLiked());
  }
}
