import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { PollsArchivePageComponent } from './components/polls-archive-page/polls-archive-page.component';
import { CurrentUserIdResolver } from 'src/app/services/security/current-user-id.resolver';

const routes: Routes = [
  {
    path: '',
    component: PollsArchivePageComponent,
    resolve: { userId: CurrentUserIdResolver },
    data: { animation: 'fadeAnimation', title: 'Опросы' }
  }
];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    providers: [CurrentUserIdResolver],
    exports: [RouterModule]
})
export class PollsRoutingModule { }
